body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-11xl: 30px;
  --font-size-3xl: 22px;
  --font-size-xl: 20px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-goldenrod: #efb725;
  --color-black: #000;
  --color-white: #fff;
  --color-gray: rgba(30, 30, 30, 0.5);

  /* border radiuses */
  --br-16xl: 35px;
  --br-19xl: 38px;
}
